html,
body {
  margin: 0;
  padding: 0;
  background-color: #E5E5E5;
  height: 100%;
  width: 100%; }

body {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  flex-wrap: wrap; }

/* Рамки вокруг блоков */
.calendar {
  width: 560px;
  height: 563px;
  padding: 72px;
  border: 1px solid;
  border-radius: 12px;
  box-sizing: border-box; }
  .calendar__month-selector {
    display: flex;
    justify-content: space-between;
    font-size: 24px;
    font-weight: 700; }
  .calendar__actions {
    display: flex; }
  .calendar__arrow {
    background-color: transparent;
    border-radius: 15%;
    border-color: transparent;
    vertical-align: top; }
    .calendar__arrow--svg {
      height: 100%; }
    .calendar__arrow--preview {
      margin-right: 15px; }
  .calendar__date-output {
    width: 416px;
    height: 334px;
    margin-top: 56px; }
  .calendar__weekdays {
    display: grid;
    height: calc(100% / 7);
    grid-template-columns: repeat(7, 1fr);
    grid-template-areas: "mo tu we th fr sa su"; }
  .calendar__weekdays-item {
    display: flex;
    align-items: center;
    justify-content: center; }
  .calendar__dates-item {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1; }
  .calendar__dates {
    display: grid;
    height: calc(100% - (100% / 7));
    grid-template-columns: repeat(7, 1fr);
    grid-template-rows: repeat(6, 1fr); }
    .calendar__dates--current-data-backlight {
      height: 56px;
      width: 56px;
      border-radius: 50%;
      position: absolute;
      z-index: -1; }
