.light-theme .calendar {
  background-color: #ffffff;
  border-color: #ebebeb;
  box-shadow: 0px 4px 32px #aaaaaa08; }

.light-theme .calendar__month-selector {
  color: #333333; }

.light-theme .calendar__arrow:hover {
  background-color: #aaaaaa; }

.light-theme .calendar__arrow--svg path {
  stroke: #333333; }

.light-theme .calendar__weekdays {
  color: #333333; }

.light-theme .calendar__dates {
  color: #666666; }

.light-theme .calendar__dates--outer-date {
  color: #b6b6b6; }

.light-theme .calendar__dates--current-data-backlight {
  background-color: #FB3F4A; }

.light-theme .calendar__dates--current-data {
  color: #FFFFFF; }

.dark-theme .calendar {
  background-color: #000000;
  border-color: #090A0C;
  box-shadow: 0px 4px 32px #aaaaaa08; }

.dark-theme .calendar__month-selector {
  color: #FFFFFF; }

.dark-theme .calendar__arrow:hover {
  background-color: #363636; }

.dark-theme .calendar__arrow--svg path {
  stroke: #FFFFFF; }

.dark-theme .calendar__weekdays {
  color: #FFFFFF; }

.dark-theme .calendar__dates {
  color: #CCCCCC; }

.dark-theme .calendar__dates--outer-date {
  color: #6d6d6d; }

.dark-theme .calendar__dates--current-data-backlight {
  background-color: #589C5F; }

.dark-theme .calendar__dates--current-data {
  color: #FFFFFF; }

/* ////////////////////////////// */
.theme-switcher {
  height: 50px;
  width: 560px;
  border-radius: 12px;
  display: flex;
  justify-content: space-evenly; }

.theme-switcher__button {
  flex-grow: 0.3;
  border-radius: 12px;
  border-color: transparent; }

.theme-switcher__button:hover {
  background-color: #535353; }
